import React from 'react'
import { Link } from 'react-router-dom'
import auth from '../Helpers/AuthService'

const Nav = props => {
    const { setUser, setAuthenticated, user, authenticated } = props

    const logout = () => {
        auth.logout()
        setUser(null)
        setAuthenticated(false)
    }

    return (
        <nav className="nav nav-pills">
            <li className="nav-item">
                <h5 className="nav-link">Zephyr Analytics, LLC</h5>
            </li>
            <li className="nav-item">
                <Link
                    className="nav-link"
                    to={authenticated && user ? `/${user._id}/profile` : '/'}
                >
                    Home
                </Link>
            </li>
            <li className="nav-item">
                {authenticated && user ? (
                    <Link className="nav-link" to={`/${user._id}/docs`}>
                        Documents
                    </Link>
                ) : (
                    <Link className="nav-link" to="/register">
                        Register
                    </Link>
                )}
            </li>
            {authenticated && user && (
                <li className="nav-item">
                    <Link className="nav-link" to={`/${user._id}/profile`}>
                        Profile
                    </Link>
                </li>
            )}
            <li className="nav-item">
                {authenticated && user ? (
                    <Link
                        onClick={() => logout()}
                        className="nav-link"
                        to="/logout"
                    >
                        Log Out
                    </Link>
                ) : (
                    <Link className="nav-link" to="/login">
                        Sign In
                    </Link>
                )}
            </li>
        </nav>
    )
}

export default Nav
