import React, { useState, useEffect } from 'react'
import { Redirect, Link } from 'react-router-dom'
// import Uppy from '@uppy/core'
// import { Dashboard } from '@uppy/react'
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
// import { Transloadit } from 'uppy'
import Axios from 'axios'

const UserDocs = props => {
    const { user, authenticated } = props
    const [docs, setDocs] = useState(null)

    useEffect(() => {
        user &&
            Axios.get(`/api/${user._id}/docs`).then(res => {
                console.log(res.data)
                setDocs(res.data)
            })
    }, [user])

    if (!authenticated || !user) {
        return <Redirect to="/" />
    }


    return (
        <div className="welcome">
            <h1>Hi, {user.username}, view your documents</h1>
            <div className="docs">
                {docs ?
                    docs.map(doc => (
                        <Link key={doc._id} to={`/docs/${doc._id}`}>
                            <img
                                className="img-thumbnail doc-preview"
                                src={doc.url}
                                alt={doc.info.name}
                            />
                        </Link>
                    )) : (
                        <div className="alert alert-danger">You have no specimens uploaded. Please create a new specimen to begin.</div>
                    )}
            </div>
        </div>
    )
}

export default UserDocs
