import React, { useState, useEffect, useRef } from 'react'
import { Redirect } from 'react-router-dom'
import Uppy from '@uppy/core'
import { Dashboard } from '@uppy/react'
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
import { Transloadit } from 'uppy'
import Axios from 'axios'
import EditProfile from './EditProfile'

const DocUpload = props => {
    const { user, authenticated } = props
    const [visible, setVisible] = useState(false)
    const [edit, setEdit] = useState(false)
    const [successful, setSuccessful] = useState(false)
    const [docs, setDocs] = useState(null)

    const uppy = useRef(
        Uppy({
            debug: true,
        }).use(Transloadit, {
            params: {
                auth: { key: '7c6af9090f1f4b7a8c64a6cbd1b4096f' },
                template_id: 'c74843b77d8f4c718999e21d194d0179',
            },
            waitForEncoding: true,
        })
    )

    const open = () => {
        setVisible(!visible)
    }
    const close = () => {
        setVisible(!visible)
        uppy.current.reset()
    }

    useEffect(() => {
        const checkForReports = () => {
            Axios.get(`/api/docs/${user._id}`, {
                params: {
                    id: user._id,
                },
            })
                .then(res => {
                    setDocs(res.data)
                })
                .catch(err => console.log('Error: ' + err))
        }

        checkForReports()
    }, [user, user._id])

    useEffect(() => {
        let curuppy = uppy.current
        return () => {
            curuppy.reset()
        }
    }, [])

    useEffect(() => {
        let currentUppy = uppy.current
        let orgId
        currentUppy.on('transloadit:result', (file, result) => {
            if (orgId === result.original_id) {
                return
            }
            console.log(result)
            orgId = result.original_id
            Axios.post('/api/docs/add', {
                user_id: user._id,
                url: result.url,
                type: result.mime,
                info: result,
            })
                .then(res => console.log('added document', res))
                .catch(err => console.log('Error: ' + err))
        })
        currentUppy.on('transloadit:complete', result => {
            setSuccessful(true)
        })
        return () => {
            currentUppy.close()
        }
    }, [user._id])

    if (!authenticated || !user) {
        return <Redirect to="/" />
    }
    return (
        <div className="welcome">
            <div className="card">
                <h3 className="mt-5">Specimen Upload</h3>
                <div>
                    {visible ? (
                        <button
                            onClick={() => close()}
                            className="btn btn-success"
                        >
                            {!successful ? 'Cancel' : 'Done'}
                        </button>
                    ) : (
                        <button
                            onClick={() => open()}
                            className="btn btn-success"
                        >
                            Begin
                        </button>
                    )}

                    {docs && (
                        <button className="btn btn-success">
                            Details Report
                        </button>
                    )}
                </div>
                {visible && <Dashboard uppy={uppy.current} debug={true} />}
            </div>
        </div>
    )
}

export default DocUpload
