import React, { useState, useEffect } from 'react'
import axios from 'axios'
import useForm from '../Helpers/CustomHooks'
import auth from '../Helpers/AuthService'

const EditProfile = props => {
    // const [isEmailValid, setIsEmailValid] = useState(false);
    const [error, setError] = useState(null)

    const { user, setEdit, setUser } = props
    const [empty, setEmpty] = useState(true)

    const updateProfile = () => {
        let keys = Object.keys(inputs)
        const profOb = {}
        keys.forEach(key => {
            console.log(user[key], inputs[key])
            profOb[key] = inputs[key]
        })
        axios
            .put(`/api/users/${user._id}`, profOb)
            .then(res => {
                console.log('success, user registered')
                console.log(res)
                console.log(res.data)
                setEdit(false)
                auth.setToken(res.data.token)
                const profile = auth.getProfile()
                setUser(profile.user)
            })
            .catch(err => {
                setError(err)
                console.log(err)
                console.log(err.message)
                console.log('Error: ' + err)
            })
    }

    useEffect(() => {
        console.log(error)
    }, [error])

    const { inputs, handleInputChange, handleSubmit } = useForm(updateProfile)

    const checkArray = array => {
        for (let index = 0; index < array.length; index++) {
            if (array[index] === '') return false
        }
        return true
    }
    useEffect(() => {
        if (inputs) {
            let keys = Object.keys(inputs)
            let values = Object.values(inputs)
            console.log(values)
            console.log(checkArray(values))
            if (Object.entries(inputs).length === 0 || !checkArray(values)) {
                setEmpty(true)
            } else {
                setEmpty(false)
            }
        }
    }, [inputs])
    console.log(inputs)
    return (
        <div className="profile-edit">
            <form>
                <div className="row">
                    <div className="form-group col-6">
                        <input
                            className="form-control zephyr-input"
                            onChange={handleInputChange}
                            defaultValue={
                                user.firstName || inputs.firstName || ''
                            }
                            type="text"
                            name="firstName"
                            id="firstName"
                            placeholder="First Name"
                        />
                    </div>
                    <div className="form-group col-6">
                        <input
                            className="form-control zephyr-input"
                            onChange={handleInputChange}
                            defaultValue={
                                user.lastName || inputs.lastName || ''
                            }
                            type="text"
                            name="lastName"
                            id="lastName"
                            placeholder="Last Name"
                        />
                    </div>
                </div>
                <div className="form-group">
                    <input
                        className="form-control zephyr-input"
                        onChange={handleInputChange}
                        defaultValue={user.username || inputs.username || ''}
                        type="text"
                        name="username"
                        id="username"
                        placeholder="Username"
                    />
                </div>
                <div className="form-group">
                    <input
                        className="form-control zephyr-input"
                        onChange={handleInputChange}
                        defaultValue={user.email || inputs.email || ''}
                        type="email"
                        name="email"
                        id="email"
                        placeholder="Email Address"
                    />
                </div>
                <div className="form-group">
                    <input
                        className="form-control zephyr-input"
                        onChange={handleInputChange}
                        defaultValue={
                            user.phoneNumber || inputs.phoneNumber || ''
                        }
                        type="tel"
                        name="phoneNumber"
                        id="phoneNumber"
                        placeholder="Telephone Number"
                    />
                </div>
                <div className="form-group">
                    <input
                        className="form-control zephyr-input"
                        onChange={handleInputChange}
                        defaultValue={inputs.password1 || ''}
                        type="password"
                        name="password1"
                        id="password1"
                        placeholder="Enter password"
                    />
                </div>
                <div className="form-group">
                    <input
                        className="form-control zephyr-input"
                        onChange={handleInputChange}
                        defaultValue={inputs.password2 || ''}
                        type="password"
                        name="password2"
                        id="password2"
                        placeholder="Confirm your password"
                    />
                </div>
                {error && <div className="alert alert-danger">Error</div>}
                <button
                    disabled={empty}
                    onClick={handleSubmit}
                    className="btn-block form-button btn btn-success"
                    type="button"
                >
                    Update Profile
                </button>
            </form>
        </div>
    )
}

export default EditProfile
