import React, { useState } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import useForm from '../Helpers/CustomHooks'
import Logo from '../imgs/logo-zephyr.png'

const UpdatePW = props => {
    const { userId, token } = props
    const [submitted, setSubmitted] = useState(false)

    const update = () => {
        axios
            .post(
                `/api/reset/receive_new_password/${userId}/${token}`,
                {
                    password: inputs.password1,
                }
            )
            .catch(err => console.log(err))
        setSubmitted(true)
    }

    const { inputs, handleInputChange, handleSubmit } = useForm(update)

    return (
        <div className="container">
            <div className="row">
                <div className="col-6 img-pane">
                    <img className="img-fluid" src={Logo} alt="logo.png" />
                </div>
                <div className="col-6 registration">
                    <h3>Update Password</h3>
                    {submitted ? (
                        <div className="reset-password-form-sent-wrapper">
                            <p>
                                If that account is in our system, we emailed you
                                a link to reset your password.
                            </p>
                        </div>
                    ) : (
                        <form onSubmit={() => update()}>
                            <div className="form-group">
                                <input
                                    className="form-control zephyr-input"
                                    onChange={handleInputChange}
                                    value={inputs.password1 || ''}
                                    type="password"
                                    name="password1"
                                    id="password1"
                                    placeholder="Enter password"
                                />
                            </div>
                            <div className="form-group">
                                <input
                                    className="form-control zephyr-input"
                                    onChange={handleInputChange}
                                    value={inputs.password2 || ''}
                                    type="password"
                                    name="password2"
                                    id="password2"
                                    placeholder="Confirm password"
                                />
                            </div>
                            <button
                                disabled={
                                    !inputs.password1 ||
                                    !inputs.password2 ||
                                    inputs.password1 !== inputs.password2
                                }
                                onClick={handleSubmit}
                                className="btn my-3 btn-block form-button btn-success"
                                type="button"
                            >
                                Reset Password
                            </button>
                        </form>
                    )}
                    <SignUpLink />
                </div>
            </div>
        </div>
    )
}

const SignUpLink = () => (
    <div className="col-12 d-flex flex-column justify-content-center">
        <p>
            Haven't Registered?{' '}
            <Link to={`${process.env.PUBLIC_URL}/register`}>Sign Up</Link>
        </p>
        <p>
            <Link to={`${process.env.PUBLIC_URL}/login`}>Back to Login</Link>
        </p>
        <p>
            Questions?{' '}
            <Link to={`${process.env.PUBLIC_URL}/contact`}>Contact Us</Link>
        </p>
    </div>
)

export default UpdatePW
