import React, { useState, useEffect, useRef } from 'react'
import { Redirect, withRouter } from 'react-router-dom'
import Uppy from '@uppy/core'
// import { Dashboard } from '@uppy/react'
import { StripeProvider, Elements } from 'react-stripe-elements'
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
import { Transloadit } from 'uppy'
import Axios from 'axios'
import CheckoutForm from './Checkout'
import EditProfile from './EditProfile'

const Welcome = props => {
    const { user, authenticated, setUser } = props
    const [visible, setVisible] = useState(false)
    const [edit, setEdit] = useState(false)
    const [successful, setSuccessful] = useState(false)
    const [docs, setDocs] = useState(null)
    const [error, setError] = useState(null)

    const uppy = useRef(
        Uppy({
            debug: true,
        }).use(Transloadit, {
            params: {
                auth: { key: '7c6af9090f1f4b7a8c64a6cbd1b4096f' },
                template_id: 'c74843b77d8f4c718999e21d194d0179',
            },
            waitForEncoding: true,
        })
    )

    const open = () => {
        setVisible(!visible)
    }
    const goTo = () => {
        props.history.push(`/${user._id}/docs`)
    }
    const close = () => {
        setVisible(!visible)
        uppy.current.reset()
    }

    useEffect(() => {
        const checkForReports = () => {
            Axios.get(`/api/${user._id}/docs`, {
                params: {
                    id: user._id,
                },
            })
                .then(res => {
                    setDocs(res.data)
                })
                .catch(err => {
                    console.log('Error: ' + err)
                    setError(err.response.data)
                })
        }

        checkForReports()
    }, [user._id])

    useEffect(() => {
        let curuppy = uppy.current
        return () => {
            curuppy.reset()
        }
    }, [])

    useEffect(() => {
        let currentUppy = uppy.current
        let orgId
        currentUppy.on('transloadit:result', (file, result) => {
            if (orgId === result.original_id) {
                return
            }
            orgId = result.original_id
            Axios.post('/api/docs/add', {
                user_id: user._id,
                url: result.url,
                type: result.mime,
                info: result,
            })
                .then(res => console.log('added document', res))
                .catch(err => {
                    setError(err.response.data)
                    console.log('Error: ' + err.response.data)
                })
        })
        return () => {
            currentUppy.close()
        }
    }, [user._id])

    if (!authenticated || !user) {
        console.log(authenticated, user)
        return <Redirect to="/" />
    }
    return (
        <div className="welcome">
            <div className="card">
                <h3 className="mt-5">
                    Welcome to Zephyr Analytics, LLC, {user.firstName}
                </h3>
                <div className="dashboard">
                    {visible ? (
                        <button
                            onClick={() => close()}
                            className="btn btn-success"
                        >
                            {!successful ? 'Cancel' : 'Return to Dashboard'}
                        </button>
                    ) : (
                        <button
                            onClick={() => open()}
                            className="btn btn-success"
                        >
                            New Specimen
                        </button>
                    )}
                    {edit ? (
                        <button
                            onClick={() => setEdit(!edit)}
                            className="btn btn-danger"
                        >
                            Cancel
                        </button>
                    ) : (
                        <button
                            onClick={() => setEdit(!edit)}
                            className="btn btn-success"
                        >
                            Edit Profile
                        </button>
                    )}

                    {docs && docs.length > 0 && (
                        <button
                            onClick={() => goTo()}
                            className="btn btn-success"
                        >
                            Details Report
                        </button>
                    )}
                </div>
                {edit && (
                    <EditProfile
                        setUser={setUser}
                        setEdit={setEdit}
                        user={user}
                    />
                )}
                {visible && (
                    <StripeProvider apiKey="pk_test_Lhd7Cxa8MO3NfgCG6FSoxaDt006NnKWtSu">
                        <Elements>
                            <CheckoutForm
                                setSuccessful={setSuccessful}
                                successful={successful}
                                user={user}
                            />
                        </Elements>
                    </StripeProvider>
                )}
            </div>
            {/* {visible && <Dashboard uppy={uppy.current} debug={true} />} */}
        </div>
    )
}

export default withRouter(Welcome)
