import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { injectStripe, CardElement } from 'react-stripe-elements'
import useForm from '../Helpers/CustomHooks'
import Checkbox from '../Elements/Checkbox'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faSpinner,
    faCheckCircle,
    faHome,
    faUpload,
} from '@fortawesome/free-solid-svg-icons'
import Logo from '../imgs/stripesmall.png'
import Visa from '../imgs/visa.png'
import MasterCard from '../imgs/master.png'
import Discover from '../imgs/discover.png'
import Amex from '../imgs/amex.png'
import Diners from '../imgs/diners.png'

const CheckoutForm = props => {
    const [ref, setRef] = useState(null)
    const [error, setError] = useState(null)
    const [saveCard, setSaveCard] = useState(false)
    const [complete, setComplete] = useState(false)
    const [customer, setCustomer] = useState(null)
    const [paymentId, setPaymentId] = useState(null)
    const [loading, setLoading] = useState(false)
    const [success, setSuccess] = useState(false)
    const [sources, setSources] = useState(null)
    const [show, setShow] = useState(false)
    const [paymentMethod, setPaymentMethod] = useState(null)
    const [active, setActive] = useState(false)

    const { stripe, user, setSuccessful, elements } = props

    const [secret, setSecret] = useState(null)
    useEffect(() => {
        axios.get('/api/payments/secret').then(res => {
            setPaymentId(res.data.paymentIntent.id)
            setSecret(res.data.client_secret)
        })
    }, [])
    useEffect(() => {
        if (customer) {
            setLoading(true)
            axios
                .get(`/api/payments/sources/${customer.id}`)
                .then(res => {
                    if (res.data.raw) {
                        setSources(null)
                    } else {
                        setSources(res.data)
                    }
                    setLoading(false)
                })
                .catch(err => {
                    console.log(err)
                })
        }
    }, [customer])
    useEffect(() => {
        if (paymentId && customer) {
            axios
                .put(`/api/payments/intent/${paymentId}`, {
                    customer: customer.id,
                    user: user._id,
                })
                .then(res => {})
        }
        if (paymentId && user) {
            axios
                .put(`/api/payments/intent/${paymentId}`, {
                    user: user._id,
                    name: `${user.firstName} ${user.lastName}`,
                    email: user.email,
                    phone: user.phoneNumber,
                })
                .then(res => {})
        }
    }, [customer, paymentId, user])

    //handle Customer data in Checkout Component
    useEffect(() => {
        if (user.customer_id) {
            axios
                .get(
                    `/api/payments/customer/${user.customer_id}`
                )
                .then(res => {
                    console.log(res.data)
                    //if customer has been deleted from stripe, remove customer id from user DB
                    if (res.data.deleted) {
                        setCustomer(null)
                        axios
                            .put(`/api/users/${user._id}`, {
                                customer_id: null,
                            })
                            .then(res => {
                                console.log(res)
                            })
                            .catch(err => {
                                console.log(err)
                            })
                        //populate component with current customer data
                    } else {
                        setCustomer(res.data)
                    }
                })
                .catch(err => {
                    setCustomer(null)
                    console.log(err)
                })
        } else {
            axios
                .get(`/api/payments/email/${user.email}`)
                .then(res => {
                    setCustomer(res.data)
                })
        }
    }, [user, user.customer_id])

    const toggleCardElement = () => {
        setShow(!show)
        setPaymentMethod(null)
        setComplete(!complete)
    }
    const toggleSave = e => {
        setSaveCard(e.target.checked)
    }
    const handleChange = event => {
        if (event.complete) {
            setError(null)
            setComplete(true)
        }
        if (event.error) {
            setError(event.error.message)
        }
        if (event.error === 'undefined') {
            setError(null)
        }
    }
    const handleCard = (source, e) => {
        if (source.id === 'new') {
            setActive(source.id)
            setShow(true)
            setPaymentMethod(null)
            setComplete(false)
        } else {
            setShow(false)
            setActive(source.id)
            setPaymentMethod(source)
            setComplete(true)
        }
    }
    const handleStripeSubmit = async () => {
        if (secret) {
            setLoading(true)
            if (paymentMethod) {
                stripe
                    .confirmCardPayment(secret, {
                        payment_method: paymentMethod.id,
                        save_payment_method: saveCard,
                    })
                    .then(res => {
                        if (ref) {
                            ref.clear()
                        }
                        if (res.error) {
                            setError(res.error.message)
                        }
                        if (res.paymentIntent.status === 'succeeded') {
                            setSuccessful(true)
                            setSuccess(true)
                            setLoading(false)
                            setComplete(false)
                        } else {
                            console.log('Success')
                        }
                    })
                    .catch(err => {
                        console.log(err)
                        setError(err.error.message)
                    })
            } else {
                stripe
                    .confirmCardPayment(secret, {
                        payment_method: {
                            card: elements.getElement('card'),
                            billing_details: {
                                email: inputs.email || user.email,
                                name:
                                    inputs.name ||
                                    `${user.firstName} ${user.lastName}`,
                            },
                        },
                    })
                    .then(res => {
                        ref.clear()
                        if (res.error) {
                            setError(res.error.message)
                        }
                        if (res.paymentIntent.status === 'succeeded') {
                            setSuccessful(true)
                            setSuccess(true)
                            setLoading(false)
                            setComplete(false)
                        } else {
                            console.log('Success')
                        }
                    })
                    .catch(err => {
                        console.log(error)
                        setError(err.error.message)
                    })
            }
            // setReceiptUrl(order.data.charge.receipt_url)
        } else {
            setError('Invalid Payment')
        }
    }

    const checkBrand = card => {
        if (card === 'Visa' || card === 'visa') {
            return Visa
        }
        if (card === 'Discover' || card === 'discover') {
            return Discover
        }
        if (card === 'MasterCard' || card === 'mastercard') {
            return MasterCard
        }
        if (card === 'Amex' || card === 'amex') {
            return Amex
        }
        if (card === 'Diners' || card === 'diners') {
            return Diners
        }
    }
    const { inputs, handleInputChange, handleSubmit } = useForm(
        handleStripeSubmit
    )
    const createOptions = () => {
        return {
            style: {
                base: {
                    fontSize: '16px',
                    color: '#495057',
                    fontFamily:
                        '-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol"',
                    '::placeholder': {
                        color: '#868e96',
                    },
                },
                invalid: {
                    color: '#9e2146',
                },
            },
        }
    }
    const newSource = {
        id: 'new',
    }

    return (
        <div className="checkout-form">
            <form onSubmit={handleSubmit}>
                {!success && sources && !loading && (
                    <div className="card-wrap">
                        {sources.map(source => (
                            <button
                                onClick={e => handleCard(source, e)}
                                type="button"
                                key={source.id}
                                className={
                                    active === source.id
                                        ? `payselected card paycard my-3 col-12`
                                        : `card paycard my-3 col-12`
                                }
                            >
                                <img
                                    className="brand"
                                    src={checkBrand(source.card.brand)}
                                    alt=""
                                />
                                â€¢â€¢â€¢â€¢ {source.card.last4}
                            </button>
                        ))}
                        <button
                            type="button"
                            onClick={e => handleCard(newSource, e)}
                            className={
                                active === newSource.id
                                    ? `payselected card paycard my-3 col-12`
                                    : `card paycard my-3 col-12`
                            }
                        >
                            Add a new debit or credit card
                        </button>
                    </div>
                )}
                {!success && !sources && !loading && (
                    <div className="card-wrap">
                        <button
                            type="button"
                            onClick={e => handleCard(newSource, e)}
                            className={
                                active === newSource.id
                                    ? `payselected card paycard my-3 col-12`
                                    : `card paycard my-3 col-12`
                            }
                        >
                            Add a new debit or credit card
                        </button>
                    </div>
                )}
                {loading && !complete && (
                    <div className="my-5 mx-auto text-center">
                        <FontAwesomeIcon icon={faSpinner} spin size="2x" />
                    </div>
                )}
                {!success && (
                    <>
                        <div className="customer">{}</div>
                        <div className="form-group text-left">
                            <input
                                className="form-control zephyr-input"
                                onChange={handleInputChange}
                                value={
                                    `${user.firstName} ${user.lastName}` ||
                                    inputs.email ||
                                    ''
                                }
                                type="name"
                                name="name"
                                id="name"
                                placeholder="First and Last Name"
                            />
                            <input
                                className="form-control zephyr-input"
                                onChange={handleInputChange}
                                value={user.email || inputs.email || ''}
                                type="email"
                                name="email"
                                id="email"
                                placeholder="Enter a valid email address"
                            />
                            <input
                                className="form-control zephyr-input"
                                onChange={handleInputChange}
                                value={user.phoneNumber || inputs.tel || ''}
                                type="tel"
                                name="tel"
                                id="tel"
                                placeholder="(123) 456-7890"
                            />
                            {show && (
                                <label>
                                    Card details
                                    <CardElement
                                        onReady={e => setRef(e)}
                                        onChange={e => handleChange(e)}
                                        {...createOptions()}
                                    />
                                </label>
                            )}
                        </div>
                        {show && (
                            <div className="form-group col-12">
                                <label>
                                    <Checkbox
                                        checked={saveCard}
                                        onChange={e => toggleSave(e)}
                                    />
                                    <span
                                        style={{
                                            marginLeft: 8,
                                            marginRight: 8,
                                        }}
                                    >
                                        Save Card
                                    </span>
                                </label>
                            </div>
                        )}
                    </>
                )}
                {loading && complete && (
                    <div className="my-5 mx-auto text-center">
                        <FontAwesomeIcon icon={faSpinner} spin size="2x" />
                    </div>
                )}
                {!loading && !success && (
                    <button
                        disabled={error || !complete}
                        type="submit"
                        className="btn btn-block mb-3 btn-success form-button"
                    >
                        Pay
                        {paymentMethod && (
                            <span>
                                {` with`}
                                <img
                                    className="brand"
                                    src={checkBrand(paymentMethod.card.brand)}
                                    alt=""
                                />
                                â€¢â€¢â€¢â€¢ {paymentMethod.card.last4}
                            </span>
                        )}
                    </button>
                )}
                {!loading && success && (
                    <div className="my-5 mx-auto text-center">
                        <FontAwesomeIcon
                            icon={faCheckCircle}
                            size="4x"
                            color="#28a745"
                        />
                        <div className="success">
                            <h2 className="mt-5 text-success">
                                Payment Success
                            </h2>
                            <div className="success-links">
                                <Link
                                    className="btn mt-5 btn-success"
                                    to="/upload"
                                >
                                    <FontAwesomeIcon
                                        icon={faUpload}
                                        size="1x"
                                        color="white"
                                    />{' '}
                                    Upload Specimens
                                </Link>
                                <p>
                                    <small>
                                        Once you have uploaded your specimens,
                                        you will need to make another purchase
                                        before uploading another specimen.
                                    </small>
                                </p>
                                <Link className="btn mt-3 btn-info" to="/">
                                    <FontAwesomeIcon
                                        icon={faHome}
                                        size="1x"
                                        color="white"
                                    />{' '}
                                    Home
                                </Link>
                            </div>
                        </div>
                    </div>
                )}

                {error && (
                    <div className="mt-3 alert alert-danger">
                        {error.message}
                    </div>
                )}
                <img className="text-center" src={Logo} alt="" />
            </form>
        </div>
    )
}

export default injectStripe(CheckoutForm)
