import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link, Redirect } from 'react-router-dom'
import useForm from '../Helpers/CustomHooks'
import auth from '../Helpers/AuthService'
import Logo from '../imgs/logo-zephyr.png'

const Register = props => {
    // const [isEmailValid, setIsEmailValid] = useState(false);
    const [error, setError] = useState(null)

    // const checkEmail = (value) => {
    //   const regex = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
    //   const checkValidity = regex.test(value);
    //   setIsEmailValid(checkValidity);
    // };
    const { user, authenticated, setAuthenticated, setUser } = props

    const register = () => {
        axios
            .post('/api/register', {
                username: inputs.username,
                password: inputs.password1,
                firstName: inputs.firstName,
                lastName: inputs.lastName,
                phone: inputs.tel,
                email: inputs.email,
            })
            .then(res => {
                console.log('success, user registered')
                console.log(res)
                console.log(res.data)
                if (res && res.data) {
                    setError(null)
                    auth.setToken(res.data.token)
                    const profile = auth.getProfile()
                    setUser(profile.user)
                    setAuthenticated(true)
                }
            })
            .catch(err => {
                setError(err)
                console.log(err.message)
                console.log('Error: ' + err)
            })
    }

    useEffect(() => {
        console.log(error)
    }, [error])

    const { inputs, handleInputChange, handleSubmit } = useForm(register)

    if (authenticated && user) {
        return <Redirect to={`/${user._id}/profile`} />
    }
    return (
        <div className="container-fluid">
            <div className="row">
                <div className="img-pane col-3">
                    <img className="img-fluid" src={Logo} alt="logo.png" />
                </div>
                <div className="col-lg-6 col-sm-12 registration">
                    <form>
                        <div className="row">
                            <div className="form-group col-6">
                                <input
                                    className="form-control zephyr-input"
                                    onChange={handleInputChange}
                                    value={inputs.firstName || ''}
                                    type="text"
                                    name="firstName"
                                    id="firstName"
                                    placeholder="First Name"
                                />
                            </div>
                            <div className="form-group col-6">
                                <input
                                    className="form-control zephyr-input"
                                    onChange={handleInputChange}
                                    value={inputs.lastName || ''}
                                    type="text"
                                    name="lastName"
                                    id="lastName"
                                    placeholder="Last Name"
                                />
                            </div>
                        </div>
                        <div className="form-group">
                            <input
                                className="form-control zephyr-input"
                                onChange={handleInputChange}
                                value={inputs.username || ''}
                                type="text"
                                name="username"
                                id="username"
                                placeholder="Username"
                            />
                        </div>
                        <div className="form-group">
                            <input
                                className="form-control zephyr-input"
                                onChange={handleInputChange}
                                value={inputs.email || ''}
                                type="email"
                                name="email"
                                id="email"
                                placeholder="Email Address"
                            />
                        </div>
                        <div className="form-group">
                            <input
                                className="form-control zephyr-input"
                                onChange={handleInputChange}
                                value={inputs.tel || ''}
                                type="tel"
                                name="tel"
                                id="tel"
                                placeholder="Telephone Number"
                            />
                        </div>
                        <div className="form-group">
                            <input
                                className="form-control zephyr-input"
                                onChange={handleInputChange}
                                value={inputs.password1 || ''}
                                type="password"
                                name="password1"
                                id="password1"
                                placeholder="Enter password"
                            />
                        </div>
                        <div className="form-group">
                            <input
                                className="form-control zephyr-input"
                                onChange={handleInputChange}
                                value={inputs.password2 || ''}
                                type="password"
                                name="password2"
                                id="password2"
                                placeholder="Confirm your password"
                            />
                        </div>
                        {error && (
                            <div className="alert alert-danger">Error</div>
                        )}
                        <button
                            onClick={handleSubmit}
                            className="btn-block form-button btn btn-success"
                            type="button"
                        >
                            Register
                        </button>
                        <SignUpLink />
                    </form>
                </div>
            </div>
        </div>
    )
}

const SignUpLink = () => (
    <div className="col-12 d-flex justify-content-center">
        <p>
            Already have an account?{' '}
            <Link to={`${process.env.PUBLIC_URL}/login`}>Sign In</Link>
        </p>
    </div>
)

export default Register
