import React, { useState, useEffect } from 'react'
import Register from './Components/Register'
import Login from './Components/Login'
import ResetPW from './Components/ResetPassword'
import UpdatePW from './Components/UpdatePassword'
import './App.css'
import { Switch, Route } from 'react-router-dom'
import Nav from './Components/Nav'
import Dashboard from './Components/Dashboard'
import DocUpload from './Components/UploadDashboard'
import UserDocs from './Components/UserDocs'
import DocView from './Components/DocView'
import PrivateRoute from './Components/PrivateRoutes'
import auth from './Helpers/AuthService'

function App() {
    const [authenticated, setAuthenticated] = useState(false)
    const [user, setUser] = useState(null)

    useEffect(() => {
        if (auth.loggedIn()) {
            setUser(auth.getProfile().user)
            setAuthenticated(true)
        } else {
            return
        }
    }, [setAuthenticated, setUser])
    return (
        <div className="App">
            <Nav
                setAuthenticated={setAuthenticated}
                setUser={setUser}
                user={user}
                authenticated={authenticated}
            />
            <Switch>
                <Route
                    path="/register"
                    render={() => (
                        <Register
                            setAuthenticated={setAuthenticated}
                            setUser={setUser}
                            user={user}
                            authenticated={authenticated}
                        />
                    )}
                ></Route>
                <Route
                    path="/login"
                    render={() => (
                        <Login
                            setAuthenticated={setAuthenticated}
                            setUser={setUser}
                            user={user}
                            authenticated={authenticated}
                        />
                    )}
                ></Route>
                <Route
                    path="/updatepw/:userId/:token"
                    render={({ match }) => (
                        <UpdatePW
                            userId={match.params.userId}
                            token={match.params.token}
                        />
                    )}
                />
                <Route
                    path="/reset"
                    render={() => (
                        <ResetPW
                            setAuthenticated={setAuthenticated}
                            setUser={setUser}
                            user={user}
                            authenticated={authenticated}
                        />
                    )}
                ></Route>
                <PrivateRoute
                    path="/:id/profile"
                    user={user}
                    setUser={setUser}
                    setAuthenticated={setAuthenticated}
                    component={Dashboard}
                    authenticated={authenticated}
                />
                <PrivateRoute
                    path="/:id/docs"
                    user={user}
                    setUser={setUser}
                    setAuthenticated={setAuthenticated}
                    component={UserDocs}
                    authenticated={authenticated}
                />
                <PrivateRoute
                    path="/docs/:id/"
                    user={user}
                    setUser={setUser}
                    setAuthenticated={setAuthenticated}
                    component={DocView}
                    authenticated={authenticated}
                />
                <PrivateRoute
                    path="/upload"
                    user={user}
                    setUser={setUser}
                    setAuthenticated={setAuthenticated}
                    component={DocUpload}
                    authenticated={authenticated}
                />
            </Switch>
        </div>
    )
}

export default App
