import React, { useState } from 'react'
import axios from 'axios'
import { Redirect, Link } from 'react-router-dom'
import useForm from '../Helpers/CustomHooks'
import Logo from '../imgs/logo-zephyr.png'

const ResetPW = props => {
    const { user, authenticated } = props
    const [submitted, setSubmitted] = useState(false)
    const [error, setError] = useState(null)

    const reset = () => {
        axios.post(`/api/reset/user/${email}`).catch(err => {
            console.log(err.response.data)
            setError(err.response.data)
        })
        if (!error) setSubmitted(true)
    }

    const { inputs, handleInputChange, handleSubmit } = useForm(reset)

    const { email } = inputs

    if (authenticated && user) {
        return <Redirect to={`/${user._id}/profile`} />
    }

    return (
        <div className="container">
            <div className="row">
                <div className="col-6 img-pane">
                    <img className="img-fluid" src={Logo} alt="logo.png" />
                </div>
                <div className="col-6 registration">
                    <h3>Reset Password</h3>
                    {!error && submitted ? (
                        <div className="reset-password-form-sent-wrapper">
                            <p>
                                If that account is in our system, we emailed you
                                a link to reset your password.
                            </p>
                        </div>
                    ) : (
                        <form onSubmit={() => reset()}>
                            <div className="form-group">
                                <input
                                    className="form-control zephyr-input"
                                    onChange={handleInputChange}
                                    value={inputs.email || ''}
                                    type="text"
                                    name="email"
                                    id="email"
                                    placeholder="Enter email address"
                                />
                            </div>
                            <button
                                onClick={handleSubmit}
                                className="btn my-3 btn-block form-button btn-success"
                                type="button"
                            >
                                Reset Password
                            </button>
                        </form>
                    )}
                    {error && (
                        <div className="mt-3 alert alert-danger">{error}</div>
                    )}
                    <SignUpLink />
                </div>
            </div>
        </div>
    )
}

const SignUpLink = () => (
    <div className="col-12 d-flex flex-column justify-content-center">
        <p>
            Haven't Registered?{' '}
            <Link to={`${process.env.PUBLIC_URL}/register`}>Sign Up</Link>
        </p>
        <p>
            <Link to={`${process.env.PUBLIC_URL}/login`}>Back to Login</Link>
        </p>
        <p>
            Questions?{' '}
            <Link to={`${process.env.PUBLIC_URL}/contact`}>Contact Us</Link>
        </p>
    </div>
)

export default ResetPW
