import React, { useState, useEffect } from 'react'
import { Redirect, withRouter, Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSpinner, faArrowLeft } from '@fortawesome/free-solid-svg-icons'
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
import Axios from 'axios'

const DocView = props => {
    const { user, authenticated } = props
    const [doc, setDoc] = useState(null)
    const docId = props.match.params.id

    useEffect(() => {
        user &&
            Axios.get(`/api/docs/${docId}`).then(res => {
                console.log(res.data)
                setDoc(res.data)
            })
    }, [docId, user])

    if (!authenticated || !user) {
        return <Redirect to="/login" />
    }
    return (
        <div className="welcome">
            <Link className="btn btn-primary" to={`/${user._id}/docs`}>
                <FontAwesomeIcon icon={faArrowLeft} /> Back to Docs
            </Link>
            <h1>{doc && doc.info.name}</h1>
            <div className="docs">
                {doc && (
                    <img
                        key={doc._id}
                        className="img-thumbnail doc-view"
                        src={doc.url}
                        alt={doc.info.name}
                    />
                )}
            </div>
        </div>
    )
}

export default withRouter(DocView)
